.name {
	@include row-flex();

	&__item {
		padding-left: 100px;

		@include md-block() {
			padding-left: 20px;
			padding-top: 50px; }

		@include sm-block() {
			padding-left: 0;
			margin-bottom: 30px; } }

	&__first {
		margin-bottom: 12px; }

	&__second {
		margin-top: 12px;
		margin-bottom: 45px; }

	&__col_6 {
		@include col();
		@include size(6); }

	&__col_sm_12 {
		@include sm-block() {
			@include size(12); } } }


.name-first {
	font-family: "OswaldRegular", sans-serif;
	font-size: 18px;
	letter-spacing: .1em;
	text-transform: uppercase; }



.name-second {
	font-family: "OswaldLight", sans-serif;
	font-size: 90px;
	line-height: 0.9;

	@include xxs-block() {
		font-size: 60px; } }
