.price {
	padding: 37px 50px 55px;
	background-color: $bg-gray;

	@include md-block() {
		margin-bottom: 30px; }

	&__sale {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 18px;
		color: #000000;
		opacity: .5; }

	&__name {
		margin-top: 15px;
		margin-bottom: 70px;
		font-family: "OswaldLight", sans-serif;
		font-size: 35px;
		line-height: 1em;
		letter-spacing: .1em;
		color: #000000;
		text-transform: uppercase; }

	&__list {
		margin: 0;
		padding: 0; }

	&__list-item {
		list-style: none;
		padding: 0;
		padding-left: 35px;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 14px;
		color: #000000;
		line-height: 1.5em;
		background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2217%22%20height%3D%2213%22%20viewBox%3D%220%200%2017%2013%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M0.196154%207.15C0.0653846%207.02%200%206.825%200%206.695C0%206.565%200.0653846%206.37%200.196154%206.24L1.11154%205.33C1.37308%205.07%201.76538%205.07%202.02692%205.33L2.09231%205.395L5.68846%209.23C5.81923%209.36%206.01538%209.36%206.14615%209.23L14.9077%200.195H14.9731C15.2346%20-0.065%2015.6269%20-0.065%2015.8885%200.195L16.8038%201.105C17.0654%201.365%2017.0654%201.755%2016.8038%202.015L6.34231%2012.805C6.21154%2012.935%206.08077%2013%205.88462%2013C5.68846%2013%205.55769%2012.935%205.42692%2012.805L0.326923%207.345L0.196154%207.15Z%22%20fill%3D%22black%22/%3E%0A%3C/svg%3E%0A');
		background-repeat: no-repeat;
		background-position: left 2px;

		&:not(:last-child) {
			margin: 29px 0; } }


	&__price {
		display: inline-block;
		font-family: "OswaldLight", sans-serif;
		font-size: 35px;
		line-height: 1em;
		letter-spacing: 0;
		color: #000000;
		margin-top: 70px; }



	&_accent {
		margin-top: -50px;
		background-color: $accent-blue;
		padding-bottom: 45px;

		@include md-block() {
			margin-top: 0; } } }



