.photo {
	padding-bottom: 70px;

	@include sm-block() {
		display: none; }

	&__container {
		@include wrapper(); }

	&__inner {
		@include row-flex(); }

	&__content {
		@include row-flex(); }

	&__item {
		max-height: 250px;
		height: auto;
		margin-bottom: 30px;

		@include xs-block() {
			width: 100%; }

		img {
			max-height: 100%;
			max-width: 100%;
			width: 100%;
			height: auto; }



		&_l {
			max-height: 530px;
			height: 100%; } }





	&__col_9 {
		@include col();
		@include size(9); }

	&__col_8 {
		@include col();
		@include size(8); }

	&__col_7 {
		@include col();
		@include size(7); }

	&__col_6 {
		@include col();
		@include size(6); }

	&__col_5 {
		@include col();
		@include size(5); }

	&__col_4 {
		@include col();
		@include size(4); }

	&__col_3 {
		@include col();
		@include size(3); }

	&__col_md_4 {
		@include md-block() {
			@include size(4); } }

	&__col_md_12 {
		@include md-block() {
			@include size(12); } }

	&__col_sm_12 {
		@include sm-block() {
			@include size(12); } }

	&__col-inner_3 {
		width: calc(33.33% - 30px);
		margin-right: 15px;
		margin-left: 15px; }

	&__col-inner_4 {
		width: calc(44.44% - 30px);
		margin-right: 15px;
		margin-left: 15px; }

	&__col-inner_5 {
		width: calc(55.55% - 30px);
		margin-right: 15px;
		margin-left: 15px; }

	&__col-inner_6 {
		width: calc(66.66% - 30px);
		margin-right: 15px;
		margin-left: 15px; } }




