.bar {
	height: 5px;
	width: 100%;
	background-color: $border-color;
	position: relative;
	overflow: hidden;




	&__progress {
		position: absolute;
		content: '';
		width: 100%;
		height: 100%;
		background-color: $accent-blue;
		left: -100%; }


	&__load {
		position: absolute;
		width: 100%;
		height: 100%;
		content: '';
		background-color: $link-color;
		left: -100%; }

	&__handler {
		content: '';
		width: 15px;
		height: 15px;
		background-color: $accent-blue;
		position: absolute;
		left: 0;
		border-radius: 50%;
		top: -5px;
		cursor: pointer;
		visibility: hidden;
		opacity: 0;
		transition: .6s ease-out;

		z-index: 100; }


	&:hover {

		.bar__handler {
			visibility: visible;
			opacity: 1; } } }


