.promo {
	padding: 80px 0;
	background: url('../img/promo-bg.jpg') #000000 no-repeat;
	background-position: right;
	background-size: cover;

	&__container {
		@include wrapper(); }

	&__inner {
		@include row-flex(); }

	&__form {
		display: flex;
		flex-direction: column;
		padding-right: 38px;

		@include md-block() {
			padding-right: 0; } }

	&__input {
		margin-bottom: 35px;
		color: #ffffff;
		border-bottom: 1px solid $border-dark;

		&:placeholder {
			color: $bg-gray; } }

	&__submit {
		margin-top: 15px; }

	&__descr {
		font-family: "FiraSansLight", sans-serif;
		font-size: 16px;
		line-height: 1.5em;
		color: #ffffff;
		opacity: .6;
		margin-top: 40px; }

	&__descr-span {
		font-family: "FiraSansRegular", sans-serif;
		font-weight: bold; }





	&__col_3 {
		@include col();
		@include size(3); }

	&__col_4 {
		@include col();
		@include size(4); }

	&__col_md_12 {
		@include md-block() {
			@include size(12); } }

	&__col_sm_12 {
		@include sm-block() {
			@include size(12); } }



	&__offset-left_md_no {
		@include md-block() {
			@include offset-left(0); } }

	&__offset-left_1 {
		@include offset-left(1); } }
