.nav {
	display: flex;
	padding: 0;
	margin: 0 -22.5px;


	&__item {
		padding: 0;
		list-style: none;
		box-sizing: border-box; }

	&__link {
		font-family: "FiraSansBold", sans-serif;
		font-size: 16px;
		text-decoration: none;
		text-transform: uppercase;
		color: #ffffff;
		margin: 0 20px;
		opacity: 0.5;
		transition: .6s ease-out;

		&:hover {
			opacity: 1; } } }
