.slider-video {
	height: 410px;
	display: flex;
	overflow-x: visible;

	@include md-block() {
		display: none; }

	&__item {
		display: flex;
		height: 100%;
		width: 100% !important;
		max-width: 870px !important;
		margin-right: 30px;

		@include md-block() {
			width: 882px !important;
			max-width: 882px !important; } }

	&__column-l {
		max-width: 570px;
		max-height: 415px;
		width: 100%;
		height: 100%;
		margin-right: 30px;

		@include sm-block() {
			width: 264px;
			height: 190px; } }

	&__column-s {
		max-width: 270px;
		max-height: 415px;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		@include md-block() {
			flex-direction: row; } }

	&__big {
		height: 100%; }

	&__small {
		max-height: 190px;
		height: 100%; }

	&__play {
		position: absolute;
		content: '';
		z-index: 2;
		transform: translate(-50%, -50%);

		&_small {
			width: 37px;
			height: 37px;
			transform: translate(-50%, -70%);
			background-size: 14px 14px; } } }


.slider-video-m {
	display: none;

	@include md-block() {
		display: flex; }

	&__play {
		position: absolute;
		content: '';
		width: 37px;
		height: 37px;
		z-index: 2;
		transform: translate(-50%, -50%);

		@include md-block() {
			background-size: 14px 14px; } }


	&__item {
		width: 264px !important;
		height: 190px;
		margin-right: 20px;
		flex-shrink: 0; } }
