.popup {
	position: fixed;
	content: '';
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	display: none;
	background-color: #ffffff;
	z-index: 1001;
	max-width: 640px;
	width: 100%;
	box-sizing: border-box;

	@include sm-block() {
		width: 90%; }

	&__video {
		width: 100%;
		height: 360px;
		cursor: pointer;
		position: relative;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;

		video {
			position: relative;
			z-index: -1; }

		@include sm-block() {
			height: 200px;

			background-position: top center; } }


	&__close {
		position: absolute;
		content: '';
		width: 30px;
		height: 30px;
		right: -30px;
		top: -30px;
		cursor: pointer;

		@include sm-block() {

			right: 0;
			top: -40px; }

		&::after {
			position: absolute;
			content: '';
			top: 15px;
			width: 100%;
			height: 2px;
			background-color: #000000;
			transform: rotate(-45deg);
			transition: .6s ease-out; }

		&::before {
			position: absolute;
			content: '';
			top: 15px;
			width: 100%;
			height: 2px;
			background-color: #000000;
			transform: rotate(45deg);
			transition: .6s ease-out; }

		&:hover {

			&::before {
				opacity: .3; }

			&::after {
				opacity: .3; } } }

	&__play {
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%); }


	&__descr {
		background-color: #ffffff;
		text-align: center;
		padding: 45px 20px; }

	&__text {
		text-transform: uppercase;
		color: #000000;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 24px;
		margin-top: 0;
		margin-bottom: 17px; }


	&_active {
		display: block; } }
