.m-menu-audio {
	background-color: #000000;
	color: #ffffff;
	font-family: "OswaldRegular", sans-serif;
	font-size: 22px;
	width: 100%;
	height: 100%;
	position: fixed;
	display: none;
	justify-content: flex-start;
	align-items: center;
	flex-direction: column;
	left: 0;
	top: 0;
	z-index: 1;

	&_active {
		display: flex;
		animation:  m-menu-audio-show .6s ease-out;
		overflow-y: scroll; }


	&__content {
		background-color: #1F1F1F;
		width: 100%;
		height: 277px;
		padding-top: 115px;
		box-sizing: border-box;
		display: flex;
		justify-content: center;
		flex-direction: column;
		flex-shrink: 0; }

	&__title {
		text-align: center;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 24px;
		line-height: 1.2em;
		margin: 0 25px; }

	&__author {
		text-align: center;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 12px;
		letter-spacing: .05em;
		color: #ffffff;
		opacity: .4;
		margin-bottom: 35px; }


	&__controls {
		display: flex;
		justify-content: space-between;
		max-width: 304px;
		width: 100%;
		margin: 0 auto;
		margin-bottom: -30px; }



	&__controls-item {
		background-color: #555555;
		width: 95px;
		height: 68px;
		border-radius: 4px;

		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 15px;
			height: 14px;
			fill: #9E9E9E; }

		&:hover {


			svg {
				fill: #000000; } } }

	&__next {

		svg {
			transform: rotate(180deg); } }

	&__list {
		padding-top: 90px;
		width: 100%;
		max-width: 304px;
		box-sizing: border-box; }

	&__item {
		margin-bottom: 25px; }

	&__play_active {
		background-color: $accent-blue;

		.play-pause_active {
			border-left: 15px solid transparent;
			border: none;
			width: 20px;
			height: 20px;
			transform: translateX(0);

			&::before {
				position: absolute;
				content: '';
				left: 0;
				top: 0;
				width: 6px;
				height: 20px;
				background-color: #000000; }

			&::after {
				position: absolute;
				content: '';
				right: 0;
				top: 0;
				width: 6px;
				height: 20px;
				background-color: #000000; } } } }



@keyframes m-menu-audio-show {
	from {
		transform: translateX(100%); }
	to {
		transform: translateX(0); } }
