.wide-slider {
	padding: 115px 0;
	display: flex;
	flex-wrap: wrap;
	box-sizing: border-box;


	&__descr {
		justify-content: flex-end;
		box-sizing: border-box;

		@include md-block() {
			margin-left: 10px; } }

	&__content {
		box-sizing: border-box;
		overflow-x: hidden;
		position: relative;
		overflow-y: hidden;

		@include md-block() {
			padding-left: 25px;
			margin-top: 27px;
			box-sizing: border-box; } }

	&__container {
		@include md-block() {
			overflow-x: scroll; } }



	&__info {
		max-width: 270px;
		width: 100%;
		margin-right: 30px;

		@include md-block() {
			margin-right: auto;
			padding-left: 15px; } }


	&__col_3 {
		display: flex;
		width: calc(50% - 285px); }

	&__col_9 {
		display: flex;
		width: calc(50% + 285px); }

	&__col_md_12 {
		@include md-block() {
			width: 100%; } } }


