.form {


	&__input {
		width: 100%;
		height: 50px;
		padding-bottom: 14px;
		padding-top: 23px;
		box-sizing: border-box;
		border: none;
		border-bottom: 1px solid $input-border;
		box-sizing: border-box;
		color: #000000;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 16px;
		line-height: 1em;
		border-radius none {}
		box-shadow: none;
		outline: none;
		background-color: transparent;
		transition: .2s ease-out;
		margin-bottom: 20px;

		&::placeholder {
			color: $date-color; }

		&:focus {
			border-bottom: 1px solid $accent-blue; }

		&_dark {
			border-bottom: 1px solid rgba(0,0,0,.4);
			color: #000000;

			&::placeholder {
				color: rgba(0,0,0,.4); }

			&:focus {
				border-bottom: 1px solid #000000; } } }




	&__col_12 {
		@include col();
		@include size(12); }


	&__col_3 {
		@include col();
		@include size(3); }

	&__col_md_6 {
		@include md-block {
			@include size(6); } }

	&__col_xs_12 {
		@include xs-block {
			@include size(12); } } }








