.hamburger {
  position: relative;
  display: flex;
  height: 100%;
  width: 100%;
  z-index: 2;
  cursor: pointer;
}

.hamburger:hover .hamburger__item {
  background-color: #73C4FF;
}

.hamburger:hover::after {
  background-color: #73C4FF;
}

.hamburger:hover::before {
  background-color: #73C4FF;
}

.hamburger__item {
  transition: .6s ease-out;
  width: 100%;
  height: 2px;
  background-color: #fff;
}

.hamburger::before {
  transition: .6s ease-out;
  position: absolute;
  opacity: 1;
  visibility: visible;
  content: '';
  width: 100%;
  height: 2px;
  background-color: #fff;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.hamburger::after {
  transition: .6s ease-out;
  position: absolute;
  content: '';
  width: 100%;
  height: 2px;
  background-color: #fff;
  bottom: 0;
}

.hamburger_active::before {
  opacity: 0;
  visibility: hidden;
}

.hamburger_active::after {
  transform: rotate(-45deg);
  transform-origin: left top;
  background-color: #73C4FF;
}

.hamburger_active .hamburger__item {
  transform: rotate(45deg);
  transform-origin: left bottom;
  background-color: #73C4FF;
}

