.agency {
	background-color: $border-color;
	max-height: 250px;
	text-decoration: none;
	position: relative;
	margin-bottom: 30px;

	&__img {
		max-height: 100%;
		max-width: 100%;
		width: 100%;
		height: 100%; }


	&::before {
		position: absolute;
		content: '';
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: transparent;
		transition: .6s ease-out; }


	&:hover {

		&::before {
			background-color: $accent-blue;
			opacity: .6; }


		.agency__link {

			&::after {
				transform: translate(100%, -50%); } } }



	&__link {
		position: absolute;
		content: '';
		font-family: "OswaldBold", sans-serif;
		font-size: 30px;
		letter-spacing: .1em;
		line-height: 1.3em;
		text-decoration: none;
		text-transform: uppercase;
		color: #ffffff;
		bottom: 45px;
		left: 45px;
		padding-right: 60px;







		&::after {
			position: absolute;
			content: '';
			width: 35px;
			height: 24px;
			background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2235%22%20height%3D%2224%22%20viewBox%3D%220%200%2035%2024%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M23.5686%200.360943C23.0918%20-0.120314%2022.2982%20-0.120314%2021.8047%200.360943C21.3279%200.825942%2021.3279%201.59986%2021.8047%202.06377L30.7487%2010.786L1.23484%2010.786C0.54684%2010.7871%200%2011.3204%200%2011.9913C0%2012.6623%200.54684%2013.2129%201.23484%2013.2129L30.7487%2013.2129L21.8047%2021.9189C21.3279%2022.4001%2021.3279%2023.1751%2021.8047%2023.6391C22.2982%2024.1203%2023.0929%2024.1203%2023.5686%2023.6391L34.6299%2012.852C35.1234%2012.387%2035.1234%2011.613%2034.6299%2011.1491L23.5686%200.360943Z%22%20fill%3D%22white%22/%3E%0A%3C/svg%3E%0A');
			background-repeat: no-repeat;
			background-size: cover;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			transition: .6s ease-out; }

		@include xs-block() {
			font-size: 20px;

			&::after {
				width: 25px;
				height: 15px; } } } }
