.slider-posts {
	display: flex;
	overflow-x: visible;

	&__container {
		display: flex !important; }

	&__item {
		display: flex;
		flex-shrink: 0;
		width: 270px;
		background-color: $bg-gray;
		text-decoration: none;
		margin-right: 30px;

		@include md-block() {
			margin-right: 25px; } }

	&__news-card-descr {
		padding: 30px; } }



.slick-next {
	display: flex;


	width: 140px !important;

	padding: 0;
	cursor: pointer;
	color: transparent;
	border: none;
	outline: none;
	background-color: $accent-blue; }

.slick-prev {
	display: none !important; }

/* Dots */
.slick-dotted.slick-slider {

	margin-bottom: 30px; }


.slick-dots {

	position: absolute;
	bottom: 35px;
	right:  30px;

	display: flex;

	width: 100%;
	padding: 0;
	margin: 0;

	list-style: none;

	text-align: center; }

.slick-dots li {

	position: relative;

	display: inline-block;

	width: 20px;
	height: 20px;
	margin: 0 5px;
	padding: 0;

	cursor: pointer; }

.slick-dots li button {

	font-size: 0;
	line-height: 0;

	display: block;

	width: 20px;
	height: 20px;
	padding: 5px;

	cursor: pointer;

	color: transparent;
	border: 0;
	outline: none;
	background: transparent; }

.slick-dots li button:hover,
.slick-dots li button:focus {

	outline: none; }

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {

	opacity: 1; }

.slick-dots li button:before {

	font-family: 'slick';
	font-size: 6px;
	line-height: 20px;

	position: absolute;
	top: 0;
	left: 0;

	width: 20px;
	height: 20px;

	content: '•';
	text-align: center;

	opacity: .25;
	color: black;

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

.slick-dots li.slick-active button:before {

	opacity: .75;
	color: black; }

.slick-list {
	width: 100%;
	min-height: 417px;
	height: 100%; }


