.sub-header {
	width: 100%;
	height: 200px;
	background-color: $bg-dark-gray;

	padding: 35px 0 30px 0;
	box-sizing: border-box;

	&__container {
		@include wrapper(); }

	&__inner {
		display: flex;
		flex-wrap: wrap; }

	&__descr {
		display: flex;
		flex-direction: column; }

	&__h1 {
		text-transform: uppercase; }

	&__search {
		margin-left: auto;
		align-self: flex-end;
		height: 20px;
		margin-bottom: -40px;

		@include xs-block() {
			margin-bottom: 0;
			margin-top: 50px;
			margin-left: 0; } }


	&_blog {
		background: url('../img/blog-bg.jpg') no-repeat center;
		background-size: cover;

		@include xs-block() {
			height: 290px; } }

	&_music {
		background: url('../img/music-bg.jpg') no-repeat center;
		background-size: cover; }

	&_appearances {
		background: url('../img/appearances-bg.jpg') no-repeat center;
		background-size: cover; } }





