.news-card {
	box-sizing: border-box;
	cursor: pointer;
	text-decoration: none;


	&:hover {
		.news-card__title {
			color: $accent-blue; } }



	&__img {
		width: 100%;
		height: 135px;
		overflow: hidden;

		img {
			width: 100%;
			max-width: 100%;
			height: auto; }

		&_size_s {
			max-height: 260px;
			height: auto;
			object-fit: cover; }

		&_size_m {
			max-height: 400px;
			height: auto;
			object-fit: cover; }

		&_size_l {
			max-height: 540px;
			height: auto;
			object-fit: cover; } }

	&__descr {
		padding-top: 20px;
		padding-bottom: 25px;

		&_no-img {
			background-color: $bg-gray;
			padding: 38px 30px; } }

	&__date {
		margin-bottom: 20px; }

	&__title {
		font-family: "OswaldRegular", sans-serif;
		font-size: 18px;
		letter-spacing: .1em;
		color: $news-card-title;
		margin-bottom: 20px;
		margin-top: 0;
		font-weight: normal;
		transition: .6s ease-out; }

	&__text {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 14px;
		line-height: 1.3em;
		color: $page-text;
		margin: 0; }

	&__col_4 {
		@include col();
		@include size(4); }

	&__col_md_6 {
		@include md-block() {
			@include size(6); } }

	&__col_sm_12 {
		@include sm-block() {
			@include size(12); } } }

