.footer {
	background-color: $footer-bg;
	padding: 47px 0 80px 0;
	color: $footer-text;
	position: relative;

	&::before {
		position: absolute;
		content: '';
		height: 100%;
		width: 1px;
		background-color: #222222;
		left: 50%;
		top: 0; }

	@include md-block() {
		padding-top: 0; }

	@include sm-block() {
		&::before {
			display: none; } }


	&__container {
		@include wrapper(); }

	&__inner {
		@include row-flex(); }

	&__phone {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 32px;
		line-height: .9em;
		color: #fff;
		text-decoration: none;
		display: block;
		margin-top: 60px; }





	&__mail {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 22px;
		line-height: .9em;
		color: #fff;
		display: block;
		margin-top: 20px; }


	&__text_gray-m {
		color: $gray-text; } }





