.play-area {
	width: 100%;
	height: 100%;
	border-radius: 50%;

	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 3;
	cursor: pointer;



	&__btn {
		max-width: 77px;
		max-height: 77px;
		width: 60%;
		height: 60%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $accent-blue;
		border-radius: 50%;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1; }

	&__pulse {
		position: absolute;
		content: '';
		width: 100%;
		height: 100%;
		background-color: #ffffff;
		z-index: -2;
		border-radius: 50%;
		opacity: .1;
		animation: pulse 3s ease-out infinite; }


	svg {
		width: 22px;
		height: 22px;
		fill: #000000; } }



