.m-menu {
	background-color: #000000;
	color: #ffffff;
	font-family: "OswaldRegular", sans-serif;
	font-size: 22px;
	width: 100%;
	height: 100%;
	position: fixed;
	display: none;
	justify-content: center;
	left: 0;
	top: 0;
	z-index: 1;

	&_active {
		display: flex;
		animation:  m-menu-show .6s ease-out; }



	&__list {
		padding: 0;
		margin: 0;
		width: 100%;
		margin-top: 150px; }


	&__item {
		list-style: none;
		text-align: center; }


	&__link {
		display: inline-block;
		color: #ffffff;
		text-decoration: none;
		padding: 17px 0;
		width: 100%;
		letter-spacing: .1em;


		&:hover {
			background-color: $accent-blue;
			color: #000000; } } }



@keyframes m-menu-show {
	from {
		transform: translateX(-100%); }
	to {
		transform: translateX(0); } }
