.btn {
	width: 100%;
	background-color: $bg-gray;
	border: none;
	border-radius: 0;
	box-shadow: none;
	cursor: pointer;
	outline: none;
	font-family: "OswaldLight", sans-serif;
	font-size: 18px;
	height: 60px;
	color: #000000;
	line-height: 1em;
	letter-spacing: .1em;
	text-align: center;
	text-decoration: none;

	&_pag {
		padding: 23px 0;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: center;


		svg {
			width: 20px;
			height: 14px;
			fill: $link-color; }

		&_active {
			background-color: $accent-blue;

			svg {
				width: 20px;
				height: 14px;
				fill: #000000; } } }

	&_social {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 40px;
		height: 40px;
		margin-right: 15px;
		border-radius: 50%;
		transition: .6s ease-out;

		&:hover {
			opacity: .8; }

		svg {
			width: 16px;
			height: 16px;
			fill: #ffffff; }

		&_vk {
			background-color: #6996C7; }

		&_facebook {
			background-color: #496194; }

		&_ok {
			background-color: #FF9C43; }

		&_insta {
			background-color: #CEBFA9; } } }

