.video {

	position: relative;
	width: 100% !important;
	height: 100%;
	cursor: pointer;
	position: relative;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	video {
		width: 100%;
		height: 100%;
		opacity: 0; }

	&__title {
		position: absolute;
		content: '';
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		background: linear-gradient(transparent, #000000);
		display: flex;
		align-items: flex-end; }

	&__title-text {
		color: #ffffff;
		text-transform: uppercase;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 24px;
		margin-bottom: 1.6em;
		margin-left: 1.6em;
		max-width: 90%;
		letter-spacing: .1em;
		line-height: 1.2em;

		&_small {
			font-size: 14px; }

		&_m {
			font-size: 11px;
			color: #ffffff;
			line-height: 1.3em;
			text-transform: uppercase;
			font-family: "FiraSansRegular", sans-serif; } }



	&__play {
		position: absolute;
		content: '';
		z-index: 2;
		transform: translate(-50%, -50%); }

	&__play_small {
		width: 37px;
		height: 37px;
		transform: translate(-50%,-70%);
		background-size: 14px 14px; }

	&_size_s {
		height: 190px; } }

