.hamburger {
	position: relative;
	display: flex;
	height: 100%;
	width: 100%;
	z-index: 2;
	cursor: pointer;

	&:hover {

		.hamburger__item {
			background-color: $accent-blue; }


		&::after {
			background-color: $accent-blue; }


		&::before {
			background-color: $accent-blue; } }






	&__item {
		transition: .6s ease-out;
		width: 100%;
		height: 2px;
		background-color: #fff; }




	&::before {
		transition: .6s ease-out;
		position: absolute;
		opacity: 1;
		visibility: visible;
		content: '';
		width: 100%;
		height: 2px;
		background-color: #fff;
		top: 50%;
		left: 0;
		transform: translateY(-50%); }


	&::after {
		transition: .6s ease-out;
		position: absolute;
		content: '';
		width: 100%;
		height: 2px;
		background-color: #fff;
		bottom: 0; }

	&_active {

		&::before {
			opacity: 0;
			visibility: hidden; }

		&::after {
			transform: rotate(-45deg);
			transform-origin: left top;
			background-color: $accent-blue; }

		.hamburger__item {
			transform: rotate(45deg);
			transform-origin: left bottom;
			background-color: $accent-blue; } } }





