.question {
	background-color: $bg-gray;
	position: relative;
	min-height: 140px;
	text-decoration: none;
	padding-left: 64px;
	display: flex;
	align-items: center;
	transition: .6s ease-out;
	margin-bottom: 30px;

	&:hover {
		background-color: $border-color;
		.question__count {
			transform: translate(-50%, -100%); } }


	&__title {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 18px;
		line-height: 1.3em;
		color: #000000;
		letter-spacing: .1em;
		text-decoration: none;
		text-transform: uppercase; }



	&__count {
		left: 0;
		top: 0;
		position: absolute;
		content: '';
		background-color: $accent-blue;
		width: 35px;
		height: 35px;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		transform: translate(-50%, -50%);
		font-family: "FiraSansBold", sans-serif;
		color: $dark-text;
		transition: .6s ease-out; }


	&__col_4 {
		@include col();
		@include size(4); }

	&__col_md_6 {
		@include md-block() {
			@include size(6); } }

	&__col_sm_11 {
		@include sm-block() {
			@include size(11); } } }


