.breadcrumbs {
	margin: 0;
	padding: 0;
	display: flex;


	&__item {

		font-family: "OswaldLight", sans-serif;
		font-size: 14px;
		line-height: 1em;
		letter-spacing: .1em;
		text-decoration: none;
		color: $link-color;
		position: relative;
		list-style: none;



		&:not(:first-child) {
			padding-left: 33px;

			&::before {
				position: absolute;
				content: '/';
				font-size: 14px;
				color: $link-color;
				top: 1px;
				left: 14px; } } }


	&__link {
		font-family: "OswaldLight", sans-serif;
		font-size: 14px;
		line-height: 1em;
		letter-spacing: .1em;
		text-decoration: none;
		color: $link-color; } }
