.info {
	padding: 60px 0 40px;
	position: relative;

	&__title {
		margin-bottom: 30px; }

	&__name {
		padding-bottom: 65px;
		padding-top: 47px;
		box-shadow: 0 4px 35px rgba(0,0,0,.1);
		margin-top: -150px;
		background-color: #ffffff;
		color: $page-title;
		position: relative;

		&::before {
			position: absolute;
			content: '';
			width: 1px;
			height: 100%;
			left: 50%;
			top: 0;
			background-color: $border-color; }

		@include md-block() {
			&::before {
				display: none; } } }

	&__biography {
		padding: 45px 100px 37px;
		margin-top: -150px;
		background-color: #ffffff;
		box-shadow: 0 4px 35px rgba(0,0,0,.1);

		@include sm-block() {
			padding: 45px 30px 37px; } }


	&__container {
		@include wrapper(); }

	&__link {
		color: $page-title; }

	&__item {
		font-family: "OswaldRegular", sans-serif;
		font-size: 30px;
		line-height: 1.3em;
		color: #000000;
		min-height: 107px;
		display: flex;
		align-items: center;
		padding-left: 30px;

		@include xs-block() {
			margin-bottom: 50px; }


		&::before {
			position: absolute;
			content: '';
			font-family: "OswaldBold", sans-serif;
			font-size: 130px;
			line-height: .7;
			color: $date-color;
			opacity: .3;
			top: 0;
			left: -5px; }


		&_1 {
			position: relative;

			&::before {

				content: '01'; } }

		&_2 {
			position: relative;

			&::before {

				content: '02'; } }

		&_3 {
			position: relative;

			&::before {

				content: '03'; } } }









	&__content {

		&_quote {
			position: relative;

			&::before {
				position: absolute;
				content: '“';
				line-height: 108px;
				left: -72px;
				top: 12px;
				font-size: 108px;
				font-family: "FiraSansRegular", sans-serif;
				color: #6A5E59;
				opacity: .2; }

			@include sm-block() {
				&::before {
					display: none; } } } }

	&__video {
		position: relative;
		width: 100%;
		height: 220px;
		margin-bottom: 30px;
		margin-top: 60px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover; }


	&__play {
		width: 125px !important;
		height: 125px !important;
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); }

	&__question {
		margin-top: 60px;
		margin-bottom: 50px;

		@include sm-block() {
			justify-content: center;
			display: flex; } }

	&__inner {
		@include row-flex(); }



	&__col_2 {
		@include col();
		@include size(2); }

	&__col_3 {
		@include col();
		@include size(3); }

	&__col_4 {
		@include col();
		@include size(4); }

	&__col_8 {
		@include col();
		@include size(8); }


	&__col_9 {
		@include col();
		@include size(9); }

	&__col_md_12 {
		@include md-block() {
			@include size(12); } }

	&__col_md_11 {
		@include md-block() {
			@include size(11); } }

	&__col_md_6 {
		@include md-block() {
			@include size(6); } }

	&__col_xs_12 {
		@include xs-block() {
			@include size(12); } }

	&__offset-left_xs_no {
		@include xs-block() {
			@include offset-left(0); } }

	&__col_sm_12 {
		@include sm-block() {
			@include size(12); } }

	&__offset-left_1 {
		@include offset-left(1); }


	&__offset-left_4 {
		@include offset-left(4); }

	&__offset-left_md_no {
		@include md-block() {
			@include offset-left(0); } }

	&__offset-left_sm_no {
		@include sm-block() {
			@include offset-left(0); } }




	&__row {
		@include row-flex();

		&_offsets {
			margin-top: 30px;
			margin-bottom: 80px; } }

	&__custom_img {
		position: absolute;
		content: '';
		left: 0;
		top: -20%;
		width: 30%;

		@include md-block() {
			top: -10px; }

		@include xs-block() {
			display: none; }


		img {
			width: 100%;
			height: auto; } }





	&_border-bottom {
		border-bottom: 1px solid $border-color; }

	&_bg-gray {
		background-color: $bg-gray; }

	&_no-pb {
		padding-bottom: 0; } }




