.article {

	&__container {
		@include wrapper(); }

	&__aside {
		padding-left: calc(8.33% + 30px);

		@include md-block() {
			display: none; } }

	&__content {
		padding: 60px 0 70px; }

	&__content-inner {
		p {

			font-family: "FiraSansRegular", sans-serif;
			font-size: 18px;
			color: $page-text;
			line-height: 1.3em;
			margin-bottom: 40px;
			margin-top: 0;


			&:not(:first-child) {
				margin-top: 20px; } }

		img {
			width: 100%;
			max-width: 100%;
			height: auto;
			margin-bottom: 20px; } }

	&__date {
		margin-left: 70px;

		@include xs-block() {
			margin-left: 20px; } }


	&__pagination {
		display: flex;
		justify-content: space-between;
		margin-top: 65px; }

	&__btn {
		width: 100%;
		max-width: 100px; }

	&__sub-header {
		padding: 60px 0 100px;
		min-height: 400px;
		box-sizing: border-box;

		background: url('../img/article__inner.jpg') #000000 no-repeat right;
		background-size: cover; }

	&__h1 {
		font-size: 30px;
		max-width: 630px;
		line-height: 1.3em;
		margin-bottom: 40px; }

	&__social {
		display: flex;
		margin-top: 50px; }

	&__row {
		display: flex;
		align-items: flex-end; }


	&__inner {
		@include row-flex(); }

	&__col_8 {
		@include col();
		@include size(8); }


	&__col_4 {
		@include col();
		@include size(4); }

	&__col_md_12 {
		@include md-block {
			@include size(12); } } }


