.b-video {
	padding-top: 100px;
	padding-bottom: 100px;

	&__container {
		@include wrapper(); }

	&_mobile-no {
		@include md-block() {
			display: none; } }

	&__mobile {
		display: none;
		padding-left: 20px;
		overflow-x: hidden;
		height: 190px;
		overflow-y: hidden;

		@include md-block() {
			display: block; } }

	&__mobile-inner {
		overflow-x: scroll;
		overflow-y: hidden; }

	&__inner {
		@include row-flex(); }

	&__video {
		margin-bottom: 30px; }

	&__col_3 {
		@include col();
		@include size(3); }

	&__col_6 {
		@include col();
		@include size(6); }

	&__col_6 {
		@include col();
		@include size(6); } }
