.footer-social {
	background-color: $footer-social;
	display: flex;
	width: 100%;
	justify-content: center;

	&__item {
		width: 100px;
		height: 100px;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		background-color: transparent;
		transition: .6s ease-out;

		&:hover {
			background-color: $accent-blue; }

		svg {
			fill: #ffffff;
			width: 20px;
			height: 20px; } } }
