.deserts {
	width: 100%;
	padding-top: 70px;
	padding-bottom: 54px;
	background-color: $bg-gray;

	@include md-block() {
		padding-bottom: 54px; }

	&__container {
		@include wrapper(); }

	&__inner {
		@include row-flex(); }


	&__item {
		display: flex;
		margin-bottom: 30px; }

	&__count {
		font-family: "OswaldRegular", sans-serif;
		font-size: 52px;
		line-height: .8em;
		letter-spacing: .1em;
		color: #000000;
		margin-right: 22px; }

	&__descr {
		max-width: 170px;
		font-family: "FiraSansRegular", sans-serif;
		color: #000000;
		font-size: 16px;
		line-height: 1.4em;
		margin-top: 0;
		margin-bottom: 0; }



	&__col_4 {
		@include col();
		@include size(4); }

	&__col_sm_6 {
		@include sm-block() {
			@include size(6); } }

	&__col_sm_6 {
		@include sm-block() {
			@include size(6); } }

	&__col_xs_12 {
		@include xs-block() {
			@include size(12); } } }
