.top {
	position: relative;

	&__social {
		position: absolute;
		content: '';
		left: 48px;
		top: 180px;
		display: flex;
		flex-direction: column;

		@include md-block() {
			display: none; } }


	&__social-link {
		text-decoration: none;


		&:not(:last-child) {
			margin-bottom: 62px; }

		&:hover {

			svg {

				fill: $accent-blue; } }

		svg {
			transition: .6s ease-out;
			width: 24px;
			height: 24px;
			fill: #ffffff; } }

	&__down {
		position: absolute;
		content: '';
		bottom: 67px;
		left: 45px;
		transform: rotate(90deg);
		animation: up-down 2s ease-out infinite;

		svg {
			width: 21px;
			height: 15px;
			fill: #ffffff; }

		@media screen and (max-width: 1350px) {
			bottom: 120px; }

		@include md-block() {
			display: none; } }

	&__content {
		width: 100%;
		height: 100%;
		position: absolute;
		content: '';
		bottom: 0;
		display: flex;
		align-items: flex-end;
		justify-content: center;

		background: url('../img/Group 3.png') no-repeat center;
		background-size: 100%;
		background-position: center bottom;

		@include sm-block() {

			background-size: 600px 368px; } }


	&__title {
		position: absolute;
		content: '';
		bottom: 175px;
		right: 15%;
		color: $page-title;

		@include md-block() {
			bottom: 500px;
			right: 50%;
			color: #ffffff;
			transform: translateX(50%); } }




	&__name-first {
		margin-bottom: 16px; }

	&__name-second {
		margin-top: 16px;
		margin-bottom: 40px; }

	&__text {
		font-family: "FiraSansReagular", sans-serif;
		font-size: 14px;
		letter-spacing: .1em;
		text-transform: uppercase; }


	&_biograpgy {
		background:  url('../img/first-scroll.jpg') $bg-dark-gray no-repet center;
		background-size: cover; } }


@keyframes up-down {
	0% {
		transform: translateY(0) rotate(90deg); }
	50% {
		transform: translateY(-20px) rotate(90deg); }
	100% {
		transform: translateY(0) rotate(90deg); } }
