.title {
	font-family: "OswaldRegular", sans-serif;
	font-size: 18px;
	text-transform: uppercase;
	line-height: 1.3em;
	letter-spacing: 0.1em;
	color: $page-title;
	font-weight: normal;

	&_l {
		text-transform: uppercase;
		font-size: 32px;
		margin-top: 15px;
		font-weight: normal;
		color: #000000; }

	&_low {
		font-family: "OswaldRegular", sans-serif;
		text-transform: none;
		font-size: 32px;
		margin-top: 15px;
		margin-bottom: 40px;
		font-weight: normal;
		color: #000000; }

	&_light {
		color: #ffffff; } }
