.audio {
	display: flex;

	&__play-btn {
		align-self: flex-end;
		margin-right: 55px;
		flex-shrink: 0;

		@include xxs-block() {
			margin-right: 15px;
			width: 35px;
			height: 35px; } }

	&__container {
		display: flex;
		flex-direction: column;
		width: 100%; }

	&__title {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 20px;
		color: $dark-gray-text;
		margin-bottom: 7px; }

	&__singer {
		font-family: "FiraSansRegular", sans-serif;
		font-size: 14px;
		color: $link-color;
		margin-bottom: 22px; } }
