.header {
	width: 100%;
	min-height: 80px;
	padding-left: 40px;
	padding-right: 50px;


	color: #ffffff;
	display: flex;
	align-items: center;
	box-sizing: border-box;
	position: relative;
	z-index: 2;
	background-color: $bg-dark-gray;

	@include md-block() {
		justify-content: space-between; }

	&__logo {
		display: flex;
		align-items: center;
		flex-shrink: 0;

		@include md-block() {
			order: 1; }


		&_large {
			width: 46px;

			img {
				width: 100%;
				min-width: 100%;
				height: auto; } } }

	&__menu {
		width: 25px;
		height: 20px;
		margin-left: auto;
		flex-shrink: 0;

		@include md-block() {
			margin-left: 0; } }





	&__nav {
		margin-left: 17%;
		flex-shrink: 0;

		@media screen and (max-width:1340px) {
			margin-left: auto; }

		@include md-block() {
			display: none; } }

	&__audio {
		display: flex;
		align-items: center;
		margin-left: 8.5%;
		flex-shrink: 0;
		position: relative;

		@media screen and (max-width:1180px) {}
		margin-left: 10px;

		@include md-block() {
			display: none; }

		&:hover {
			background-color: $footer-social; }

		&:hover  .header__audio-item-inner {

			display: block; } }


	&__audio-item {
		padding: 0 30px;
		height: 80px;
		display: flex;
		align-items: center; }



	&__audio-item-inner {
		position: absolute;
		content: '';
		left: 0;
		top: 100%;
		background-color: $footer-social;
		display: none; }



	&__audio-title {
		color: #ffffff;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 16px;
		margin-bottom: 2px; }

	&__audio-singer {
		color: #ffffff;
		opacity: .4;
		font-family: "FiraSansRegular", sans-serif;
		font-size: 12px;
		margin-bottom: 0; }

	&__audio-m {
		display: none;
		width: 40px;
		height: 40px;
		position: relative;
		order: 1;

		@include md-block() {
			display: flex; }

		&__play-area {

			.play-area__btn {
				background-color: #DDDDDD; }

			svg {
				height: 8px;
				width: 8px; } } }


	&_transparent {
		background-color: transparent; }

	&_large {
		padding-top: 38px; }

	&__play-btn {
		width: 40px;
		height: 40px;
		margin-right: 15px;
		flex-shrink: 0;
		background-size: 6px 6px; }



	&_fixed {
		position: fixed;
		z-index: 30;
		background-color: #1F1F1F; }

	&_m-menu {
		background-color: #000000; } }
