.music {
	padding: 50px 0 110px;

	&__container {
		@include wrapper(); }

	&__inner {
		@include row-flex(); }

	&__col_6 {
		@include col();
		@include size(6); }

	&__col_md_8 {
		@include md-block() {
			@include size(8); } }

	&__col_sm_12 {
		@include sm-block() {
			@include size(12); } }

	&__name-first {
		color: #000000;
		font-size: 18px;
		font-family: "OswaldRegular", sans-serif;
		letter-spacing: .1em;
		line-height: 1em;
		margin-bottom: 6px; }

	&__name-second {
		color: #000000;
		font-size: 60px;
		font-family: "OswaldLight", sans-serif;
		letter-spacing: 0;
		line-height: .9em;
		margin-top: 6px;
		margin-bottom: 56px; } }
