.top-learning {
	width: 100%;
	min-height: 622px;
	min-height: 100vh;
	padding-top: 110px;
	box-sizing: border-box;
	background-color: #000000;
	background: url('../img/top-learning-bg.jpg') no-repeat right;
	background-size: cover;
	padding-bottom: 60px;
	box-sizing: border-box;


	&__container {
		@include wrapper(); }

	&__row {
		display: flex;
		margin-right: -15px;
		margin-left: -15px;
		flex-wrap: wrap; }



	&__header {
		font-family: "OswaldRegular", sans-serif;
		letter-spacing: .1em;
		line-height: 1.4em;
		font-size: 50px;
		color: #ffffff;
		font-weight: normal;
		text-transform: uppercase;
		max-width: 464px;
		margin-top: 20px;
		margin-bottom: 70px;

		@include xxs-block() {
			font-size: 40px; } }

	&__descr {
		font-family: "OswaldLight", sans-serif;
		font-size: 20px;
		line-height: 1em;
		letter-spacing: .1em;
		color: #ffffff;
		text-transform: uppercase; }

	&__entry {
		max-width: 260px;
		margin-right: 15px;
		margin-left: 15px;

		@include xs-block() {
			width: 100%;
			max-width: 100%;
			margin-bottom: 20px; } }

	&__more {
		max-width: 170px;
		margin-right: 15px;
		margin-left: 15px;

		@include xs-block() {
			width: 100%;
			max-width: 100%; } } }
