.music-card {
	width: 100%;


	&__img {
		width: 100%;
		max-height: 250px;
		background-color: $bg-gray;
		object-fit: cover;

		img {
			width: 100%;
			height: auto; } }

	&__content {
		padding: 20px 0 26px;
		background-color: #F9F9F9;
		border: 1px solid $border-color;
		border-top: none;

		@include xxs-block() {
			padding: 20px 10px 26px; } }

	&__audio {
		padding: 24px 40px 44px;

		@include sm-block() {
			padding: 24px 8px; }

		&:hover {
			background-color: $bg-gray; } }

	&__play-btn {
		@include sm-block() {
			margin-right: 10px;
			background-size: 14px 14px; } } }
