.main-btn {
	border: none;
	box-shadow: none;
	border-radius: 190px;
	width: 100%;
	height: 50px;
	padding: 16px 0 17px;
	box-sizing: border-box;
	background-color: $accent-blue;
	color: #000000;
	font-family: "FiraSansRegular", sans-serif;
	font-size: 16px;
	line-height: 1.2em;
	cursor: pointer;
	transition: .6s ease-out;
	display: inline-block;
	text-align: center;
	text-decoration: none;

	&:hover {
		transform: translateY(-4px); }


	&_shine {
		font-size: 14px;
		box-shadow: 0 4px 44px rgba(115,196,255,.6); }

	&_dark {
		background-color: #000000;
		color: #ffffff; }

	&_border {
		background-color: transparent;
		color: $accent-blue;
		border: 1px solid $accent-blue;
		font-size: 14px; } }
