.play {
	content: '';
	border-radius: 50%;
	position: absolute;
	transform: translate(-50%, -50%);
	background-color: $accent-blue;
	display: flex;
	left: 50%;
	top: 50%;
	align-items: center;
	justify-content: center;
	cursor: pointer;

	&_area {
		position: absolute;
		z-index: 1;

		span {
			position: absolute;
			content: '';
			z-index: -2;
			width: 125px;
			height: 125px;
			border-radius: 50%;
			background-color: #ffffff;
			opacity: .1;
			animation: pulse 2s ease-out infinite; } }


	svg {
		width: 22px;
		height: 24px; } }

